import Vue from 'vue'

Vue.prototype.serviceType = {
  role: '/role', // 
  history: '/campaign/history', // 
  campaign: '/campaign', // 
  notify: '/notify', // 
  goods: '/goods', // 
  shop: '/shop', // 
  discount: '/discount', // 
  order: '/order' // 
}

Vue.prototype.apiType = {
  getDiscount: {url: Vue.prototype.serviceType.discount + '/query', method: 'POST'},
  getOrder: {url: Vue.prototype.serviceType.order + '/query', method: 'POST'},
  confirmDiscount: {url: Vue.prototype.serviceType.discount + '/confirm', method: 'GET'},
  confirmOrder: {url: Vue.prototype.serviceType.order + '/confirm', method: 'GET'},
  getToken: {url: '/token', method: 'GET'},
  login: {url: '/login', method: 'GET'}, // 
  ssoLogin: {url: '/sso', method: 'GET'}, // SSO 
  logoff: {url: '/logoff', method: 'GET'}, // 
  getMenu: {url: '/menu/query', method: 'GET'}, // 
  getUserInfo: {url: '/info', method: 'GET'}, // 
  updateUserInfo: {url: '/info', method: 'POST'}, // 
  getRoleInfo: {url: Vue.prototype.serviceType.role + '/self', method: 'GET'}, // 
  updateRole: {url: Vue.prototype.serviceType.role + '/update', method: 'POST'}, // 
  querySubordinate: {url: Vue.prototype.serviceType.role + '/subordinate', method: 'GET'}, // 
  getCaptcha: {url: '/captcha.jpg', method: 'GET'}, // 
  queryShop: {url: Vue.prototype.serviceType.shop + '/query', method: 'POST'}, // ()
  updateShop: {url: Vue.prototype.serviceType.shop + '/update', method: 'POST'}, // ()
  deleteShop: {url: Vue.prototype.serviceType.shop + '/', method: 'DELETE'}, // ()
  getShopTemplate: {url: Vue.prototype.serviceType.shop + '/template', method: 'GET', fileName: '().xls'}, // ()
  uploadShop: {url: Vue.prototype.serviceType.shop + '/upload', method: 'POST'}, // ()
  queryGoods: {url: Vue.prototype.serviceType.goods + '/query', method: 'POST'}, // ()
  queryBindGoods: {url: Vue.prototype.serviceType.goods + '/bind', method: 'GET'}, // ()
  updateGoods: {url: Vue.prototype.serviceType.goods + '/update', method: 'POST'}, // ()
  deleteGoods: {url: Vue.prototype.serviceType.goods + '/', method: 'DELETE'}, // ()
  getGoodsTemplate: {url: Vue.prototype.serviceType.goods + '/template', method: 'GET', fileName: '().xls'}, // ()
  uploadGoods: {url: Vue.prototype.serviceType.goods + '/upload', method: 'POST'}, // ()
  bindGoods: {url: Vue.prototype.serviceType.goods + '/bind', method: 'POST'} // ()
}

export default {}



